// hooks/useShipments.ts
"use client";

import { useState, useEffect } from "react";
import { BACKEND_URL } from "@/lib/config";
import {
  ShipmentFromApi,
  ShipmentsResponse,
  HistoryShipment,
} from "@/lib/types/shipments.types";
import { useI18n } from "@/i18n/i18nProvider";

const PAGE_SIZE = 30;

export function useShipments() {
  const { t } = useI18n();

  const [companyFilter, setCompanyFilter] = useState("");
  const [selectedDate, setSelectedDate] = useState("");
  const [sortOrder, setSortOrder] = useState<"newest" | "oldest">("newest");

  const [shipments, setShipments] = useState<ShipmentFromApi[]>([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [currentPage, setCurrentPage] = useState(1);
  const [total, setTotal] = useState(0);

  const [historyOpen, setHistoryOpen] = useState(false);
  const [historyShipment, setHistoryShipment] =
    useState<HistoryShipment | null>(null);

  const totalPages = Math.max(Math.ceil(total / PAGE_SIZE), 1);

  async function loadShipments(page: number) {
    try {
      setError(null);
      setLoading(true);

      const res = await fetch(
        `${BACKEND_URL}/api/shipments?page=${page}&pageSize=${PAGE_SIZE}&companyName=${companyFilter}&date=${selectedDate}&sort=${sortOrder}`
      );

      if (!res.ok) throw new Error();
      const data = (await res.json()) as ShipmentsResponse;

      setShipments(data.items);
      setTotal(data.total);
      setCurrentPage(data.page);
    } catch {
      setError(t("dashboard.loadError"));
    } finally {
      setLoading(false);
    }
  }

  useEffect(() => {
    loadShipments(1);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  useEffect(() => {
    loadShipments(1);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [companyFilter, selectedDate, sortOrder]);

  // Tady transformujeme ShipmentFromApi -> HistoryShipment
  const onShowHistory = (s: ShipmentFromApi) => {
    setHistoryShipment({
      id: s.id,
      trackingNumber: s.trackingNumber,
      companyName: s.company.name,
      status: s.status, // Assuming `s` has a `status` property
      uploadedAt: s.uploadedAt, // Assuming `s` has an `uploadedAt` property
      timestamp: s.uploadedAt, // Assuming `s` has a `timestamp` property
    });
    setHistoryOpen(true);
  };

  return {
    companyFilter,
    setCompanyFilter,
    selectedDate,
    setSelectedDate,
    sortOrder,
    setSortOrder,
    shipments,
    loading,
    error,
    total,
    currentPage,
    totalPages,
    historyOpen,
    setHistoryOpen,
    historyShipment,
    onShowHistory,
    loadShipments,
  };
}
