"use client";

import { useState, useRef } from "react";
import { useI18n } from "@/i18n/i18nProvider";

import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogDescription,
  DialogFooter,
  DialogTrigger,
} from "@/components/ui/dialog";

import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";

import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table";

import { ChevronRight, Trash2, Plus } from "lucide-react";
import { BACKEND_URL } from "@/lib/config";

import {
  InvoiceRecordJson,
  PreviewRow,
  parseInvoiceFile,
  isJsonFile,
} from "@/lib/types/invoices.types";

// ---------------------------
// LOCALLY USED TYPE
// ---------------------------
type ParsedFile = {
  id: string;
  file: File;
  rows: PreviewRow[];
  records: InvoiceRecordJson[];
  expanded: boolean;
};

type UploadInvoicesDialogProps = {
  onUploaded?: () => void;
};

export default function UploadInvoicesDialog({
  onUploaded,
}: UploadInvoicesDialogProps) {
  const { t } = useI18n();
  const fileInputRef = useRef<HTMLInputElement | null>(null);

  const [open, setOpen] = useState(false);
  const [parsedFiles, setParsedFiles] = useState<ParsedFile[]>([]);
  const [error, setError] = useState<string | null>(null);
  const [isUploading, setIsUploading] = useState(false);
  const [isParsing, setIsParsing] = useState(false);
  const [isDragging, setIsDragging] = useState(false);

  const resetState = () => {
    setParsedFiles([]);
    setError(null);
    setIsUploading(false);
    setIsParsing(false);
    setIsDragging(false);
  };

  const handleOpenChange = (nextOpen: boolean) => {
    setOpen(nextOpen);
    if (!nextOpen) resetState();
  };

  // ---------------------------
  // ADD FILES
  // ---------------------------
  const addFiles = async (filesToAdd: File[]) => {
    if (!filesToAdd.length) return;
    setIsParsing(true);
    setError(null);

    const newParsed: ParsedFile[] = [];

    for (const file of filesToAdd) {
      if (!isJsonFile(file)) continue;

      const parsed = await parseInvoiceFile(file);
      if (!parsed) continue;

      const { records } = parsed;

      const rows: PreviewRow[] = records.map((r) => ({
        id: r.id,
        trackingNumber: r.trackingNumber,
        companyName: r.companyName ?? "-",
        provider: r.provider ?? "-",
        originCountry: r.originCountry ?? "-",
        destinationCountry: r.destinationCountry ?? "-",
        invoicedPrice: r.invoicedPrice,
        invoicedWeight: r.invoicedWeight ?? 0,
      }));

      newParsed.push({
        id: file.name,
        file,
        rows,
        records,
        expanded: false,
      });
    }

    if (newParsed.length) {
      setParsedFiles((prev) => [...prev, ...newParsed]);
    }

    setIsParsing(false);
  };

  // Trigger input after drop/click
  const handleFileInputChange = async (
    e: React.ChangeEvent<HTMLInputElement>
  ) => {
    const fileList = e.target.files;
    if (!fileList) return;
    await addFiles(Array.from(fileList));
    e.target.value = "";
  };

  // ---------------------------
  // DRAG & DROP METHODS
  // ---------------------------
  const handleDragOver = (e: React.DragEvent<HTMLDivElement>) => {
    e.preventDefault();
    setIsDragging(true);
  };

  const handleDrop = async (e: React.DragEvent<HTMLDivElement>) => {
    e.preventDefault();
    setIsDragging(false);
    const files = Array.from(e.dataTransfer.files).filter(isJsonFile);
    await addFiles(files);
  };

  // ---------------------------
  // UPLOAD TO BACKEND
  // ---------------------------
  const handleConfirmUpload = async () => {
    if (!parsedFiles.length) return;

    const allRecords: InvoiceRecordJson[] = parsedFiles.flatMap(
      (pf) => pf.records
    );

    setIsUploading(true);

    try {
      const res = await fetch(`${BACKEND_URL}/api/invoices/upload`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(allRecords),
      });

      if (!res.ok) throw new Error(await res.text());

      onUploaded?.();
      resetState();
      setOpen(false);
    } catch (err) {
      console.error(err);
      setError(t("uploadDialog.errorUpload"));
    } finally {
      setIsUploading(false);
    }
  };

  // ---------------------------
  // RENDER
  // ---------------------------
  return (
    <Dialog open={open} onOpenChange={handleOpenChange}>
      <DialogTrigger asChild>
        <Button className="rounded-full bg-[#00C3A0] text-white hover:bg-[#009E85]">
          {t("dashboard.uploadInvoices")}
        </Button>
      </DialogTrigger>

      <DialogContent className="max-w-[900px] max-h-[80vh] overflow-y-auto">
        <DialogHeader>
          <DialogTitle>{t("uploadDialog.title")}</DialogTitle>
          <DialogDescription>{t("uploadDialog.helperText")}</DialogDescription>
        </DialogHeader>

        {error && (
          <div className="text-red-600 bg-red-50 px-4 py-2 rounded-md">
            {error}
          </div>
        )}

        {isParsing && (
          <Badge variant="outline">{t("uploadDialog.parsing")}</Badge>
        )}

        {/* FILES TABLE */}
        {parsedFiles.map((pf) => (
          <div key={pf.id} className="border rounded-lg my-4 bg-white">
            <div className="flex justify-between p-4">
              <div>
                <strong>{pf.file.name}</strong>
                <p className="text-xs text-slate-400">
                  {pf.rows.length} records
                </p>
              </div>
              <Trash2
                onClick={() =>
                  setParsedFiles((prev) => prev.filter((f) => f.id !== pf.id))
                }
              />
            </div>
          </div>
        ))}

        <DialogFooter>
          <Button variant="outline" onClick={() => setOpen(false)}>
            {t("uploadDialog.cancel")}
          </Button>
          <Button
            disabled={!parsedFiles.length || isUploading}
            onClick={handleConfirmUpload}
            className="bg-[#00C3A0] hover:bg-[#009E85] text-white"
          >
            {isUploading ? "Uploading..." : t("uploadDialog.confirm")}
          </Button>
        </DialogFooter>
      </DialogContent>
    </Dialog>
  );
}
