// hooks/useShipmentHistory.ts
"use client";

import { useState, useEffect } from "react";
import { HistoryShipment } from "@/lib/types/history.types"; // Adjust the path based on your project structure
import { BACKEND_URL } from "@/lib/config";

export function useShipmentHistory(shipmentId?: string) {
  const [history, setHistory] = useState<HistoryShipment[]>([]);
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    if (!shipmentId) return;

    fetch(`${BACKEND_URL}/api/shipments/${shipmentId}/history`)
      .then((r) => r.json())
      .then((data) => setHistory(data))
      .finally(() => setLoading(false));
  }, [shipmentId]);

  return { history, loading };
}
