export type InvoiceRecordJson = {
  id: string;
  trackingNumber: string;
  companyName?: string;
  provider?: string;
  originCountry?: string;
  destinationCountry?: string;
  invoicedPrice: number;
  invoicedWeight?: number;
};

export type PreviewRow = {
  id: string;
  trackingNumber: string;
  companyName: string;
  provider: string;
  originCountry: string;
  destinationCountry: string;
  invoicedPrice: number;
  invoicedWeight: number;
};

export async function parseInvoiceFile(file: File): Promise<{
  records: InvoiceRecordJson[];
} | null> {
  try {
    const text = await file.text();
    const json = JSON.parse(text);

    if (!Array.isArray(json)) return null;

    return {
      records: json.map((r: Partial<InvoiceRecordJson>) => ({
        id: r.id ?? crypto.randomUUID(),
        trackingNumber: r.trackingNumber ?? "-",
        companyName: r.companyName ?? "-",
        provider: r.provider ?? undefined,
        originCountry: r.originCountry ?? undefined,
        destinationCountry: r.destinationCountry ?? undefined,
        invoicedPrice: Number(r.invoicedPrice ?? 0),
        invoicedWeight: Number(r.invoicedWeight ?? 0),
      })),
    };
  } catch {
    return null;
  }
}

export function isJsonFile(file: File): boolean {
  return file.type === "application/json" || file.name.endsWith(".json");
}
